import React from 'react';
import ReactDOM from 'react-dom';
import ImgFigure from './components/singimg';
import ControllerUnity from './components/ControllerUnit'

import './components/style/imgshow.scss';

let Constant={
				centerPos:
				{
					left:0,
					right:0
				},
				hPosRange://
				{    
					leftSecX:[0,0],
					rightSecX:[0,0],
					y:[0,0]
				},
				vPosRange://
				{   
					x:[0,0],
					topY:[0,0]
				}
			};

let getRangeRandom=function(low, high){
		return Math.ceil(Math.random()*(high-low)+low);
	};


//-30--30
let get30DegRandom=function(){
	var value=Math.random()*30;
	return Math.random()>0.5 ? value : -value;
}

// # gallery []
class Gallery extends React.Component{
	constructor(){
		super();
		this.state={
			ImgData:require('./data/imageData.json'),
			imgsArrangeArr:[   //
				/*
				{
					pos:{
						left:'0',
						top:'0'
					},
					rotate:0
					isInverse:false,    //
					isCenter:false
				}
				*/
			]
		},
		this.inverse=this.inverse.bind(this);
		this.rearRagnge=this.rearRagnge.bind(this);
	}
	//() 
	/**
	*@param index inverse
	*@return {Function}   
	*  index
	***/
	inverse(index){
		return function(){
			console.log(""+index);
			var imgsArrangeArr=this.state.imgsArrangeArr;
			imgsArrangeArr[index].isInverse=!imgsArrangeArr[index].isInverse;  //
			this.setState({
				imgsArrangeArr
			});
			if(!this.state.imgsArrangeArr[index].isCenter){
				this.rearRagnge(index);   //
			}
		}.bind(this);
		//console.log(this.state.imgsArrangeArr);
	}
	

	getInitialState(){}

	componentWillMount(){
		let obj={
				pos:{
					left:0,
					top:0
				},
				rotate:0,         //
				isInverse:false,
				isCenter:false   
			};
		var Arr=Array(this.state.ImgData.length).fill(obj); 
		this.setState({
			imgsArrangeArr:Arr
		});					
	}

	// 
	componentDidMount(){
		//ref(dom)
		//
		var stageDOM=ReactDOM.findDOMNode(this.refs.stage),
			stageW=stageDOM.scrollWidth,
			stageH=stageDOM.scrollHeight,
			halfStageW=Math.ceil(stageW/2),   //
			halfStageH=Math.ceil(stageH/2);
		//imgFigure
		var imgFigureDOM=ReactDOM.findDOMNode(this.refs.imgFigure0),
			imgW=imgFigureDOM.scrollWidth,
			imgH=imgFigureDOM.scrollHeight,
			halfImgW=Math.ceil(imgW/2),
			halfImgH=Math.ceil(imgH/2);

		//
		Constant={
				centerPos:{
					left:halfStageW-halfImgW,
					right:halfStageH-halfImgH
				},
				hPosRange:{    //
					leftSecX:[-halfImgW, halfStageW-halfImgW*3],
					rightSecX:[halfImgW+halfImgW, stageW-halfImgW],
					y:[-halfImgH, stageH-halfImgH]
				},
				vPosRange:{   //
					x:[halfStageW-imgW, halfStageW],
					topY:[-halfImgH, halfStageH-halfImgH*3]
				}
			};
		this.rearRagnge(0);   //
		//------------------------------------------------------------6
		//console.log(this.state.imgsArrangeArr);
	}

	
	//
	// @param centerIndex  

	rearRagnge(centerIndex){
		let imgsArrangeArr=this.state.imgsArrangeArr,   //len=15
			centerPos=Constant.centerPos,
			hPosRange=Constant.hPosRange,
			vPosRange=Constant.vPosRange,

			hPosRangeLeftSecX=hPosRange.leftSecX,
			hPosRangeRightSecX=hPosRange.rightSecX,
			hPosRangeY=hPosRange.y,
			vPosRangeTopY=vPosRange.topY,
			vPosRangeX=vPosRange.x,

			imgsArrangeTopArr=[],
			topImgNum=Math.random()>0.5 ? 1 : 0,   //
			topImgSpliceIndex=0,
			imgsArrangeCenterArr=imgsArrangeArr.splice(centerIndex,1);   //len=14
			//centerIndex 
			imgsArrangeCenterArr[0].pos=centerPos;
			imgsArrangeCenterArr[0].rotate=0;
			imgsArrangeCenterArr[0].isCenter=true;
			//----------------------------------------------
			//console.log(imgsArrangeCenterArr);
	

			//
			// # Math.round  
			// # Math.ceil   
			// # Math.floor  
			topImgSpliceIndex=Math.floor(Math.random()*(imgsArrangeArr.length-topImgNum));
			imgsArrangeTopArr=imgsArrangeArr.splice(topImgSpliceIndex,topImgNum);  //len=13
			//
			imgsArrangeTopArr.forEach((value, index)=>{
				imgsArrangeTopArr[index]={
					pos:{
						top:getRangeRandom(vPosRangeTopY[0], vPosRangeTopY[1]),
						left:getRangeRandom(vPosRangeX[0], vPosRangeX[1])
					},
					rotate:get30DegRandom(),
					isInverse:imgsArrangeTopArr[index].isInverse,
					isCenter:false
				};
			});
			//----------------------------------------------
			//console.log(imgsArrangeTopArr);


			//
			for(var i=0,j=imgsArrangeArr.length,k=j/2; i<j; i++){
				var hPosRangeLORX=null;
				// 
				if(i<k){
					hPosRangeLORX=hPosRangeLeftSecX;
				}else{
					hPosRangeLORX=hPosRangeRightSecX;
				}

				imgsArrangeArr[i]={
					pos:{
						top:getRangeRandom(hPosRangeY[0], hPosRangeY[1]),
						left:getRangeRandom(hPosRangeLORX[0], hPosRangeLORX[1])
					},
					rotate:get30DegRandom(),
					isInverse:imgsArrangeArr[i].isInverse,
					isCenter:false
				};
			}

			if(imgsArrangeTopArr && imgsArrangeTopArr[0]){
				imgsArrangeArr.splice(topImgSpliceIndex, 0, imgsArrangeTopArr[0]);
			}

			imgsArrangeArr.splice(centerIndex, 0, imgsArrangeCenterArr[0]);

			this.setState({
				imgsArrangeArr:imgsArrangeArr
			});
	}

	render(){
		var ControllerUnitys=[];
		return (
			<section className="stage" ref="stage">
				<section className="img-sec">
					<h1></h1>
					{
						this.state.ImgData.map((item, index)=>{
							/**
							if (!this.state.imgsArrangeArr[index]){  //
								let tempArr=[...this.state.imgsArrangeArr, obj];
								//this.state.imgsArrangeArrslice(0).push()
								this.setState({imgsArrangeArr:tempArr});
							}
							***/
							//console.log(this.state.imgsArrangeArr[index]);

							ControllerUnitys.push(<ControllerUnity arrange={this.state.imgsArrangeArr[index]} inverse={this.inverse(index)} key={index}/>);    //
							return <ImgFigure key={index} data={item} ref={`imgFigure${index}`} arrange={this.state.imgsArrangeArr[index]} inverse={this.inverse(index)}/>;
						})
					}
				</section>
				<nav className="controller-nav">{ControllerUnitys}</nav>
			</section>
		);
	}
}

export default Gallery;
ReactDOM.render(<Gallery/>, document.getElementById('root'));


